@import "../partials/variables";

//Style 07
#atw_style7{
    .atw_tabs{
        .nav{
            display: flex;
            justify-content: center;
            flex-wrap: wrap;
            margin-bottom: 40px;
            li{
                margin-right: 30px;
                &:last-child{
                    margin-right: 0;
                }
                a{
                    font-size: 22px;
                    color: #9192a3;
                    transition: $transition;
                    padding-bottom: 10px;
                    display: block;
                    &.active{
                        color: #000;
                        border-bottom: 2px solid $theme2;
                    }
                }
            }
        }
        .tab-content{
            .tab-pane{
                .atw_slider_wrapper{
                    position: relative;
                    .atw--single_item{
                        &:hover{
                            .atw_item_top{
                                .atw_product_actions2{
                                    bottom: 10px;
                                    opacity: 1;
                                    visibility: visible;
                                }
                            }
                        }
                        .atw_item_top{
                            position: relative;
                            overflow: hidden;
                            .atw_product_actions2{
                                position: absolute;
                                left: 0;
                                bottom: -20px;
                                width: 100%;
                                visibility: hidden;
                                opacity: 0;
                            }
                        }
                        .atw_item_bottom{
                            padding: 20px;
                            border-top: 1px solid $borderColor;
                        }
                    }
                    .atw_slider_controls{
                        .slider--control7a,
                        .slider--control7b,
                        .slider--control7c{
                            border: 1px solid $borderColor;
                            border-radius: 50%;
                            z-index: 2;
                            position: absolute;
                            top: 50%;
                            transform: translateY(-50%);
                            transition: $transition;
                            &:hover{
                                border-color: $theme1;
                                color: $theme1;
                            }
                        }
                        .icon-arrow-left{
                            left: -20px;
                        }
                        .icon-arrow-right{
                            right: -20px;
                        }
                    }
                }
            }
        }
    }
}